
/*profile background*/



#profile {
  background-image: url('http://carlosrull.com/wp-content/uploads/2013/06/macbookair_command_line.jpg');
  background-color: #410972;
  background-blend-mode: darken;
  background-size: auto;
  background-attachment: fixed;
  background-position: centre;
  background-repeat: no-repeat; 
   -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;  min-height: 100%;

    min-width: 100%;
  min-height: 100%;

  }

/*other backgrounds*/

.navbar {
    background-color: $navbar-color-background;}

   #education {
    background-color: $third-color-background;
  }

#skills, #experience {
background-color: $second-color-background;    
